import React, { useState, useEffect } from "react";
import MaterialTable from "material-table";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import { connect } from "react-redux";
import {GetAllBooks} from "../../actions/Books.js"
import propTypes from "prop-types";


const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  }
};
const useStyles = makeStyles(styles);
const   Booktable = (props) =>{
  const classes = useStyles();
  const [Loading , setLoading] = React.useState(true)
  const [activities, setActivities] = React.useState([]);
  const [data , setData] = React.useState({})
  const [state, setState] = React.useState({
    columns: [
      { title: "Book", field: "book_name" },
      { title: "Author", field: "author" },
      { title: "Uploaded by", field: "User"} ,
      { title: "status", field: "status"},
      { title: "medium", field: "medium"}
    ]
  });


  const editBook = (event , rowData) =>{
    const link = "/admin/ViewBook/" + rowData.id
    props.history.push(link);
  }
  useEffect( () => {
    props.GetAllBooks();
  } ,[props.auth])
  
  
useEffect( () => {
  setData(props.items.Totalbooks);
  console.log(props.items.Totalbooks)
}, [props.items.Totalbooks])

  return (

    <MaterialTable
      title="Book List"
      columns={state.columns}
      data={data}
      actions={[
        {
          icon: 'edit',
          onClick: editBook
        },
        {
          icon: 'delete',
          tooltip: 'Save User',
          onClick: (event, rowData) => alert("Book to be deleted " + rowData.id)
        }
      ]}

      
    />
  );
}
Booktable.propTypes ={
  GetAllBooks: propTypes.func.isRequired
};
const mapStateToProps =(state) =>({
  auth: state.auth,
  errors: state.errors,
  items: state.items
});

export default connect(mapStateToProps, {GetAllBooks})(Booktable);