import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import PropTypes from "prop-types";
import Edit from "@material-ui/icons/Edit";
import Download from "@material-ui/icons/GetApp";
import DeleteIcon from "@material-ui/icons/Delete";
import { NavLink } from "react-router-dom";
const useStyles = makeStyles({
  root: {
    minWidth: 475
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)"
  },
  title: {
    fontSize: 14
  },
  pos: {
    marginBottom: 12
  }
});

export default function CardBookDescription(props) {
  const classes = useStyles();
  const { Title, Description, Author, id ,book_name , ...rest } = props;
  const link = "/admin/ViewBook/" + id + "/edit";
  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography
          className={classes.title}
          color="textSecondary"
          gutterBottom
        >
          <NavLink style={{textDecoration: 'none'}} to={link}><Edit /></NavLink><br />
          {Title}
        </Typography>
        <Typography variant="h5" component="h2">
          {Description}
          <br />
          <br />
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
          Author
        </Typography>
        <Typography variant="h5" component="h2">
          {Author}
        </Typography>
      </CardContent>
    </Card>
  );
}
CardBookDescription.propTypes = {
  Title: PropTypes.string,
  Description: PropTypes.string,
  Author: PropTypes.string,
  id: PropTypes.string,
  book_name : PropTypes.string
};
