//Route to add Book
//Method = POST
//Access = Private
export const PostBook = (items , history) => dispatch => {
  dispatch(clearErrors());
  dispatch(clearItems());
  axios
   .post("/api/post/book", items)
         .then(res =>history.push(`/admin/ViewBook/${res.data.Book_id}`))
         .catch( err => 
          dispatch({
              type: GET_ERRORS,
              payload: err
          })
         );
      };
