//Route to get all Categories
//Method = Get
//Called in Dashboard Page , Addinng subCategory Page
export const GetCategories = () => dispatch => {
  dispatch(setCategoryLoading());
  dispatch(clearItems());
  axios
   .get("/api/get/Category")
    .then(res =>
      dispatch({
        
        type: GET_CATEGORIES,
        payload: res.data
      }) )
    .catch(err =>
       dispatch({
         type: GET_CATEGORIES,
         payload: []
       })
      );
}
