import React, { useState, useEffect } from "react";
// react plugin for creating charts
// @material-ui/core
import { makeStyles } from "@material-ui/core/styles";
import Icon from "@material-ui/core/Icon";
import CategoryCard from "components/Card/CategoryCard.js";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import propTypes from "prop-types";

import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardIcon from "components/Card/CardIcon.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import { NavLink } from "react-router-dom";
//Icons
import LibraryBooksSharpIcon from "@material-ui/icons/LibraryBooksSharp";
import CategorySharpIcon from "@material-ui/icons/CategorySharp";
import GroupSharpIcon from "@material-ui/icons/GroupSharp";
import CloudDownloadSharpIcon from "@material-ui/icons/CloudDownloadSharp";
//Style
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import { connect } from "react-redux";
import {GetCategories ,Getstats} from "../../actions/Books.js"
import LinearProgress from '@material-ui/core/LinearProgress';
const  Dashboard = (props) =>{
  const useStyles = makeStyles(styles);
  const classes = useStyles();
  const [Loading , setLoading] = React.useState(true)
  const [categories, setCategories] = React.useState([]);
  const [usercount , setUsercount] = React.useState("");
  const [categorycount , setCategorycount] = React.useState("");
  const [bookcount , setBookcount] = React.useState("");
  const [time , setTime] = React.useState("");
  const [downloads , setDownloads] = React.useState("")

  useEffect( () => {
    props.GetCategories();
    props.Getstats();
  } ,[props.auth])
  
useEffect( () => {
  const { stats} = props.stats;
  console.log(stats)
  stats.map((key => {
  setTime(key.Timestamp)
  setUsercount(key.UserCount);
  setCategorycount(key.CategoryCount);
  setBookcount(key.BookCount);
  setDownloads(key.DownloadCount)
  }))
  
}, [props.stats])

  
useEffect( () => {
  setCategories(props.items.Categories);
  setLoading(false);
  console.log(props.items.Categories)
}, [props.items.Categories])

useEffect( () => {
  setLoading(props.items.loading)
  console.log(Loading)
}, [props.items.loading])

let Content;

    if(Loading) {
      Content =<GridContainer>
                      <GridItem xs={12} sm={4} md={12}>
                       <LinearProgress color="secondary"/>
                      </GridItem>
                    </GridContainer> ;
    } else if(categories.length> 0){
      Content =  <GridContainer>
                   {categories.map((category) => 
                     <GridItem xs={12} sm={6} md={3}>
                     <CategoryCard
                       image={category.poster}
                       title={category.category_name}
                       description={category.category_Desc.slice(0, 20) + "..."}
                       category_id = {category.id}
                       ViewLink='/admin/Dashboard/'
                       EditLink="/admin/Dashboard/"
                     />
                   </GridItem>      )}
             </GridContainer>
    }else{
      Content = <GridContainer>
                    <GridItem xs={12} sm={4} md={12}>
                     <h1>Welcome to Book Cms</h1>
                    </GridItem>
                  </GridContainer> ;
    }


  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="warning" stats icon>
              <CardIcon color="warning">
                <LibraryBooksSharpIcon />
              </CardIcon>
              <p className={classes.cardCategory}>Total Books</p>
              <h3 className={classes.cardTitle}>{bookcount}</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>updated on {time}</div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="success" stats icon>
              <CardIcon color="success">
                <CategorySharpIcon />
              </CardIcon>
              <p className={classes.cardCategory}>Total categories</p>
               <h3 className={classes.cardTitle}>{categorycount}</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>updated on {time}</div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="success" stats icon>
              <CardIcon color="warning">
                <GroupSharpIcon />
              </CardIcon>
              <p className={classes.cardCategory}>Total Users</p>
              <h3 className={classes.cardTitle}>{usercount}</h3>
            </CardHeader>
            <CardFooter stats>
              <div className={classes.stats}>updated on {time}</div>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card>
            <CardHeader color="success" stats icon>
              <CardIcon color="success">
                <CloudDownloadSharpIcon />
              </CardIcon>
              <p className={classes.cardCategory}>Total Downloads</p>
              <h3 className={classes.cardTitle}>{downloads}</h3>
            </CardHeader>
            <CardFooter stats>
             <div className={classes.stats}>updated on {time}</div>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
      {Content}
    </div>
  );
}
Dashboard.propTypes ={
  GetCategories: propTypes.func.isRequired,
  Getstats: propTypes.func.isRequired
};
const mapStateToProps =(state) =>({
  auth: state.auth,
  errors: state.errors,
  items: state.items,
  stats: state.stats
});

export default connect(mapStateToProps, { GetCategories , Getstats})(Dashboard);