import React, { useState, useEffect } from "react";
import MaterialTable from "material-table";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import { connect } from "react-redux";
import {GetActivity} from "../../actions/Books.js"
import propTypes from "prop-types";


const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  }
};
const useStyles = makeStyles(styles);
const   ActivityTable = (props) =>{
  const classes = useStyles();
  const [Loading , setLoading] = React.useState(true)
  const [activities, setActivities] = React.useState([]);
  const [data , setData] = React.useState({})
  const [state, setState] = React.useState({
    columns: [
      { title: "User", field: "user" },
      { title: "Action", field: "action" },
      { title: "Item", field: "item"} ,
      { title: "Item_name", field: "item_name"},
      {
        title: "Timestamp",
        field: "Timestamp",
        type: "timestamp"
      
      }
    ]
  });


  
  useEffect( () => {
    if(!props.auth.isAdmin){
      props.history.push("/admin/Dashboard")
    }
    props.GetActivity();
  } ,[props.auth])
  
  
useEffect( () => {
  setActivities(props.items.Activities);
  setData(props.items.Activities);
  console.log(props.items.Activities)
}, [props.items.Activities])

  return (

    <MaterialTable
      title="Activities"
      columns={state.columns}
      data={data}
      
    />
  );
}
ActivityTable.propTypes ={
  GetActivities: propTypes.func.isRequired
};
const mapStateToProps =(state) =>({
  auth: state.auth,
  errors: state.errors,
  items: state.items
});

export default connect(mapStateToProps, { GetActivity})(ActivityTable);