import React, { useState, useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardBooklg from "components/Card/CardBooklg.js";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CardBookDescription from "components/Card/CardBookDescription";
import List from "components/Lists/lists";
import { connect } from "react-redux";
import {ViewBookPage} from "../../actions/Books.js"
import propTypes from "prop-types";
import AddIcon from '@material-ui/icons/Add';
import axios from 'axios';
import LinearProgress from '@material-ui/core/LinearProgress';
const styles = {
  typo: {
    paddingLeft: "25%",
    marginBottom: "40px",
    position: "relative"
  },
  note: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    bottom: "10px",
    color: "#c0c1c2",
    display: "block",
    fontWeight: "400",
    fontSize: "13px",
    lineHeight: "13px",
    left: "0",
    marginLeft: "20px",
    position: "absolute",
    width: "260px"
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    display: "inline"
  },
  IconButton:{
    float: "right",
    display: "inline",
    marginTop: "10px",
    marginRight: "10px"
    
  }
};

const useStyles = makeStyles(styles);

const BookPage = (props)=> {
  const [ books , setBooks] = React.useState([]);
  const [ bookName , setBookName] = React.useState("");
  const [ author , setAuthor] = React.useState("");
  const [ description , setDescription] = React.useState("");
  const [ chapters , setChapters] = React.useState([])
  const [ solutions , setSolutions] = React.useState([])
  const [ bookloading , setBookLoading] = React.useState(true)
  const [ chapterloading , setChapterLoading] = React.useState(true)
  const [ solutionloading , setSolutionLoading] = React.useState(true)
  const [ book_url , setIsBook] =React.useState(false);
  useEffect( () => {
    axios
    .get(`/api/getChild/Chapter/${props.match.params.id}`)
     .then(res =>{
      setChapters(res.data);
       setChapterLoading(false);
      } )
     .catch(err =>{
        console.log(err);
     });   
  } ,[props.auth])

  useEffect( () => {
    axios
    .get(`/api/getChild/Solution/${props.match.params.id}`)
     .then(res =>{
      setSolutions(res.data);
      setSolutionLoading(false);
      } )
     .catch(err =>{
        console.log(err);
     });   
  } ,[props.auth])

  
 const addbook = ()=> {
  props.history.push("/admin/ViewBook/" + props.match.params.id + "/UploadBook")
}
const addchapter = ()=> {
 props.history.push("/admin/ViewBook/" + props.match.params.id + "/UploadChapter")
}
const addsolution = ()=> {
 props.history.push("/admin/ViewBook/" + props.match.params.id + "/UploadSolution")
}

 useEffect( () => {
   props.ViewBookPage(props.match.params.id, "Book")
 } ,[props.auth])

 useEffect( () => {
   setBooks(props.items.Books);
   console.log(props.items.Books);
   setBookLoading(false);
   console.log(book_url)
 }, [props.items.Books])
 



 let chapterlist;
  if(chapterloading){
    chapterlist=  <CardBody>
                     <LinearProgress color="secondary"/>
                 </CardBody>



  }else if(chapters.length > 0){
    chapterlist =  <CardBody>
                      {chapters.map((chapter) =>
                      <List
                        label = {chapter.chapter_name}
                        type ="Chapter"
                        id={chapter.id}
                        DownloadLink={chapter.url}
                       /> )}
                      </CardBody>

  }else{
    chapterlist = <CardBody>
                     <h4>No Chapters</h4>
                  </CardBody>

  }

  let  solutionlist;
  if(solutionloading){
    solutionlist=  <CardBody>
                     <LinearProgress color="secondary"/>
                   </CardBody>

  }else if(solutions.length > 0){
     solutionlist = <CardBody>
                       {solutions.map((solution) =>
                       <List
                         label = {solution.chapter_name}
                         id={solution.id}
                         type ="Solution"
                         DownloadLink={solution.url}
                        /> )}
                       </CardBody>


  }else{
    solutionlist=  <CardBody>
                     <h4>No Solutions</h4>
                   </CardBody>
    
  }

  let booklist;
  if(bookloading){
    booklist = <CardBody>
                  <LinearProgress color="secondary"/>
                </CardBody>


  }else if(books.length > 0){
    booklist=  <CardBody>
                {books.map((book) => 
                  <List
                    label={book.book_name}
                    id={book.id}
                    type= "Book"
                    DownloadLink={book.url}
                  />
                  )}
                  </CardBody>

  }else{
    booklist=      <CardBody>                  
                   </CardBody>
    

  }
 
  const classes = useStyles();
  return (
   
    <GridContainer>
       {books.map((book) => 
       <Card>
       <CardHeader color="primary">
        <h1 className={classes.cardTitleWhite}>{book.book_name}</h1>
       </CardHeader>
       <CardBody>
         <GridContainer>
           <GridItem xs={12} sm={12} md={4}>
             <CardBooklg image={book.poster} title={book.book_name} id={book.id}/>
           </GridItem>
           <GridItem xs={12} sm={12} md={8}>
             <CardBookDescription
               Title="Description"
               Description={book.description}
               Author={book.author}
               id={book.id}
               book_name = {book.book_name}
             />
           </GridItem>
         </GridContainer>
       </CardBody>
     </Card>
      
      )}
      
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h1 className={classes.cardTitleWhite}>Download Complete Book</h1>
              <AddIcon onClick={addbook} className ={classes.IconButton} />
            </CardHeader>
             {booklist}
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h1 className={classes.cardTitleWhite}>Download Chapters</h1>
              <AddIcon onClick={addchapter} className ={classes.IconButton} />
              <h3 className={classes.cardCategoryWhite}>Chapter List</h3>
            </CardHeader>
              {chapterlist}
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h1 className={classes.cardTitleWhite}>Download Solutions</h1>
              <AddIcon onClick={addsolution} className ={classes.IconButton} />
              <h3 className={classes.cardCategoryWhite}>Solution Lists</h3>
            </CardHeader>
            {solutionlist}
          </Card>
        </GridItem>
      </GridContainer>
    </GridContainer>
  );
}

BookPage.propTypes ={
  ViewSubcategories: propTypes.func.isRequired,
  id: propTypes.string,
  type:propTypes.string
 };
 const mapStateToProps =(state) =>({
   auth: state.auth,
   errors: state.errors,
   items: state.items
 });
 
 export default connect(mapStateToProps, {ViewBookPage})(BookPage);
 