import React, { useState, useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
// @material-ui/core
import Icon from "@material-ui/core/Icon";
import CardBook from "components/Card/CardBook.js";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import GridItem from "components/Grid/GridItem";

import { connect } from "react-redux";
import {ViewMyBooks} from "../../actions/Books.js";
import propTypes from "prop-types";
import LinearProgress from '@material-ui/core/LinearProgress';

const styles = {
  typo: {
    paddingLeft: "25%",
    marginBottom: "40px",
    position: "relative"
  },
  note: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    bottom: "10px",
    color: "#c0c1c2",
    display: "block",
    fontWeight: "400",
    fontSize: "13px",
    lineHeight: "13px",
    left: "0",
    marginLeft: "20px",
    position: "absolute",
    width: "260px"
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);

const UserDashBoard =(props) =>{
const classes = useStyles();
const [Loading , setLoading] = React.useState(true)
const [Books, setBooks] = React.useState([]);

  useEffect( () => {
    props.ViewMyBooks()
  } ,[props.auth])
  

  useEffect( () => {
    setBooks(props.items.Mybooks);
    setLoading(false);
    console.log(props.items.Mybooks)
  }, [props.items.Mybooks])

  let Content;

  if(Loading) {
    Content =<GridContainer>
                    <GridItem xs={12} sm={4} md={12}>
                    <h3>Loading...</h3>
                    <LinearProgress color="secondary"/>
                    </GridItem>
                  </GridContainer> ;
  } else if(Books.length > 0){
    Content =  <GridContainer>
                      {Books.map((book) => 
                      <GridItem xs={12} sm={4} md={3}>
                      <CardBook image={book.poster} title={book.book_name} id={book.id} />
                    </GridItem>

                         )}
           </GridContainer>
  } else{
    Content = <h3>You have no Books</h3>
  }





  return (
    <Card>
      <CardHeader color="primary">
        <h2 className={classes.cardTitleWhite}>Your Books</h2>
      </CardHeader>
      <CardBody>
        {Content}
      </CardBody>
    </Card>
  );
}
UserDashBoard.propTypes ={
  ViewMyBookss: propTypes.func.isRequired
};
const mapStateToProps =(state) =>({
  auth: state.auth,
  errors: state.errors,
  items: state.items
});

export default connect(mapStateToProps, {ViewMyBooks})(UserDashBoard );